
import React, { useState } from 'react';
import './App.css';

import Basket from './components/Basket';
import { Product, Products } from './components/products';
import { productsData } from './components/productsData';
import Footer from './components/Footer';

/**
 * =============================================
 *                  App.js
 * =============================================
 * App.js is a main controller for all the 
 * components what the application uses.
 * =============================================
 * The components are currently: 
 *    <Products />
 *    <Footer />
 * =============================================
 * Summary 12.11.2021
 * =============================================
 */


function App() {
  const { product } = productsData;
  const [cartItems, setCartItems] = useState([]);
  const [forms, setForms] = useState([]);

  const onAdd = (product) => {
    const exist = cartItems.find(x => x.id === product.id)
    if (exist) {
      setCartItems(cartItems.map(x => x.id === product.id ? { ...exist, qty: exist.qty + 1 } : x));
    } else {
      setCartItems([...cartItems, {...product, qty: 1}])
    }
  }

  const onRemove = ( product ) => {
    const exist = cartItems.find((x) => x.id === product.id);
    if (exist.qty === 1) {
      setCartItems(cartItems.filter((x) => x.id !==product.id));
    } else {
      setCartItems(cartItems.map(x => x.id === product.id ? { ...exist, qty: exist.qty - 1 } : x));
    }
  }

  return (
    <div className="App">
      <header id="navbar" className="row block center">
        <nav className="navbar-container container">
          <a href="/" className="home-link">
            <div className="navbar-logo"></div>
            Fanikauppa
          </a>
          <button
            type="button"
            id="navbar-toggle"
            aria-controls="navbar-menu"
            aria-label="Toggle menu"
            aria-expanded="false"
          >
            <span className="icon-bar"></span>
            <span className="icon-bar"></span>
            <span className="icon-bar"></span>
          </button>
          <div id="navbar-menu" aria-labelledby="navbar-toggle">
            <ul className="navbar-links">
              <li className="navbar-item"><a className="navbar-link" href="/about">About</a></li>
              <li className="navbar-item"><a className="navbar-link" href="/blog">Blog</a></li>
              <li className="navbar-item"><a className="navbar-link" href="/careers">Careers</a></li>
              <li className="navbar-item"><a className="navbar-link" href="/contact">Contact</a></li>
            </ul>
          </div>
        </nav>
      </header>

      <header className="App-header">
        <h1>Fani Kauppa</h1>
      </header>

      <div className="container">
        <div className="row">
          <div className="row">
            <Products onAdd={onAdd} products={productsData} />
            <Basket onAdd={onAdd} cartItems={cartItems} onRemove={onRemove} setForms={setForms} forms={forms}/>
          </div>
          <footer style={{ color: "black" }}>
            <h5>footer</h5>
          </footer>
        </div>
      </div>
    </div>
  );
}

export default App;
