import '../App.css';
import React, { useState } from 'react';
import Card from "react-bootstrap/Card";
import { productsData } from './productsData';

/**
 * =============================================
 *                  products.js
 * =============================================
 * products.js is an component that App.js calls.
 * 
 * This component calls productsData.js and its data.
 * 
 * =============================================
 * Summary 12.11.2021
 * =============================================
 */


const Product = ({ product, onAdd }) => {

    return (
        <div>
            <div>
                <img src={product.img}></img>
                <p>{product.nimi}</p>
                <p>{product.price}€</p>
                <p>{product.desc}</p>
                <div>
                    <button onClick={() => onAdd(product)}>Lisää ostoskoriin {product.amount}</button>
                </div>
            </div>
        </div>
    )
}

const Products = ({ products, setProducts, onAdd }) => {


    return (
        <div>
            <h2>Tuotteet</h2>
            {products.map(p =>
                <Product key={p.id} product={p} onAdd={onAdd}/>)}
        </div>
    )
}

export { Product, Products };
