import React, { useState } from "react";

const Basket = ({ cartItems, onAdd, onRemove, forms, setForms }) => {


    const itemsPrice = cartItems.reduce((a, c) => a + c.qty * c.price, 0);
    const taxPrice = itemsPrice * 0.14;
    const shippingPrice = itemsPrice > 2000 ? 0 : 20;
    const totalPrice = itemsPrice + taxPrice + shippingPrice;

    const setConfirmPrice = (confirmPrice) =>{
        totalPrice.cartItems = confirmPrice;
        Object.freeze(totalPrice)
    }

    console.log(itemsPrice);

    const [newName, setNewName] = useState("");
    const [newEmail, setNewEmail] = useState("");
    const [newPhone, setNewPhone] = useState("");
    const [newAddress, setNewAddress] = useState("");



    return (
        <aside className="block .col-1">
            <h2>Ostoskorisi</h2>
            <div>{cartItems.length === 0 && <div>Ostoskorisi on tyhjä</div>}</div>

            {cartItems.map((item) => (
                <div key={item.id} className="row">
                    <div className="col-2">{item.nimi}</div>
                    <div className="col-2">
                        <button onClick={() => onAdd(item)} className="add">+</button>
                        <button onClick={() => onRemove(item)} className="remove">-</button>
                    </div>
                    <div className="col-2 text-right">
                        {item.qty} x €{parseFloat(item.price).toFixed(2)}
                    </div>
                </div>
            ))}
            {cartItems.length !== 0 && (
                <>
                    <hr></hr>
                    <div className="row">
                        <div className="col-2">Tuotteen hinta</div>
                        <div className="col-1 text-right">€{parseFloat(itemsPrice).toFixed(2)}</div>
                    </div>

                    <div className="row">
                        <div className="col-2">Vero</div>
                        <div className="col-1 text-right">€{parseFloat(taxPrice).toFixed(2)}</div>
                    </div>

                    <div className="row">
                        <div className="col-2">Toimitus</div>
                        <div className="col-1 text-right">€{parseFloat(shippingPrice).toFixed(2)}</div>
                    </div>

                    <div className="row">
                        <div className="col-2"><strong>Yhteensä</strong></div>
                        <div className="col-1 text-right"><strong>€{parseFloat(totalPrice).toFixed(2)}</strong></div>
                    </div>

                    <hr></hr>
                    <div className="row">
                            <form onSubmit={e => {
                                e.preventDefault();
                                setForms(forms.concat({ nimi: newName, email: newEmail, phone: newPhone, address: newAddress }));
                                setNewName("");
                                setNewEmail("");
                                setNewPhone("");
                                setNewAddress("");
                                alert(`Kiitos tilauksesta ${newName} lähetämme tuotteen osoitteeseen ${newAddress}. Kuitti tulostuu teidän sähköpostiinne ${newEmail} ja ${newPhone}. Tuotteiden hinnat ovat yhteensä ${totalPrice}€`);
                                window.location.reload(false)
                            }}>
                                <label for="name">Nimi</label><br></br>
                                <input type="text" value={newName} onChange={e => setNewName(e.target.value)} required></input><br></br>

                                <label for="email">Sähköposti</label><br></br>
                                <input type="text" value={newEmail} onChange={e => setNewEmail(e.target.value)} required></input><br></br>

                                <label for="phone">Puhelin</label><br></br>
                                <input type="text" value={newPhone} onChange={e => setNewPhone(e.target.value)} required></input><br></br>

                                <label for="address">Osoite</label><br></br>
                                <input type="text" value={newAddress} onChange={e => setNewAddress(e.target.value)} required></input><br></br>

                                <button type="submit" value="Submit" onClick={()=> setConfirmPrice}>Vahvista tilaus</button>

                            </form>
                    </div>
                </>
            )}
        </aside>
    )
}

export default Basket;